<template>
  <div class="contents">
    <div class="form-wrapper form-wrapper-sm">
      <form @submit.prevent="submitForm" class="form">
        <div>
          <label for="username">id: </label>
          <input id="username" type="text" v-model="username" />
          <input
            type="text"
            v-if="emailInput"
            placeholder="인증번호를 입력해주세요."
            v-model="authNumber"
          />
          <button type="button" class="btn" @click="emailAuth">인증요청</button>
          <button
            type="button"
            class="btn"
            @click="emailConfrm"
            style="margin-left:1%;"
            v-if="emailInput"
          >
            인증확인
          </button>
          <p class="validation-text">
            <span class="warning" v-if="!isUsernameValid && username">
              Please enter an email address
            </span>
          </p>
        </div>
        <div>
          <label for="password">pw: </label>
          <input id="password" type="password" v-model="password" />
        </div>
        <div>
          <label for="nickname">nickname: </label>
          <input id="nickname" type="text" v-model="nickname" />
        </div>
        <button type="submit" class="btn">회원 가입</button>
      </form>
      <p class="log">{{ logMessage }}</p>
    </div>
  </div>
</template>

<script>
import { registerUser, emailAuth } from '@/api/auth';
import { validateEmail } from '@/utils/validation';

export default {
  data() {
    return {
      // form values
      username: '',
      password: '',
      nickname: '',
      // log
      logMessage: '',
      emailauth: false,
      emailInput: false,
      emailAuthNumber: '',
      authNumber: '',
    };
  },
  methods: {
    async submitForm() {
      if (!this.emailauth) {
        alert('이메일 인증이 필요합니다.');
        return false;
      }

      if (!this.password) {
        alert('비밀번호를 입력해주세요.');
        return false;
      } else if (!this.nickname) {
        alert('닉네임을 입력해주세요.');
        return false;
      }

      const userData = {
        username: this.username,
        password: this.password,
        nickname: this.nickname,
      };
      const { data } = await registerUser(userData);
      console.log(data.username);
      this.logMessage = `${data.username} 님이 가입되었습니다`;
      this.$router.push('/main');
      this.initForm();
    },
    initForm() {
      this.username = '';
      this.password = '';
      this.nickname = '';
    },
    async emailAuth() {
      if (!validateEmail(this.username)) {
        alert('이메일 형식이 아닙니다.');
        return false;
      }
      this.emailInput = !this.emailInput;
      const { data } = await emailAuth({ username: this.username });
      this.emailAuthNumber = data.data;
      console.log(this.emailAuthNumber);
    },
    emailConfrm() {
      if (this.authNumber != this.emailAuthNumber) {
        alert('인증번호를 확인해주세요.');
        return false;
      }
      alert('인증되었습니다.');
      this.emailauth = !this.emailauth;
    },
  },
  computed: {
    isUsernameValid() {
      return validateEmail(this.username);
    },
  },
};
</script>

<style>
.btn {
  color: white;
}
</style>
